.story {
	padding: 90px 0 115px 0;
	h2 {
		text-align: center;
		&::after {
			@include keyline(calc(50% - 30px), 60px, #E5E5E5);
		}
	}
	.story-description-info {
		float: left;
		width: 50%;
		h3 {
			padding: 0 0 25px 0;
			font-family: $heading-font;
			font-size: 40px;
		}
		.overview-heading {
			text-transform: uppercase;
			font-size: 25px;
		}
		p {
			padding-bottom: 25px;
			line-height: 2;
		}
		
	}
	.story-description-image {
		width: 525px;
		height: 295px;
		padding-left: 20px;
		overflow: hidden;
	}
	.story-synopsis-image {
		display: inline-block;
		width: 370px;
		height: 210px;
		padding-right: 20px;
		overflow: hidden;
	}
}

.story-description {
	padding: 50px 0;
	&:nth-of-type(1) .story-description-info {
		padding-right: 30px;
		text-align: right;
	}
	&:nth-of-type(2) .story-description-info {
		padding-left: 30px;
	}
}

.half-container	{
	float: left;
	width: 50%;
	&:first-child {
		text-align: right;
	}
}