// ID for contact form on contact page
#wpcf7-f86-p84-o1, #wpcf7-f86-p70-o2 {
	margin: 50px auto 0 auto;
	padding-bottom: 120px;
	label {
		color: transparent;
	}
	p:nth-of-type(1), p:nth-of-type(3	) {
		float: left;
		width: calc(50% - 25px);
	}
	p:nth-of-type(1) {
		margin-right: 25px;
	}
	p:nth-of-type(3) {
		margin-left: 25px;
	}
	p:last-of-type {
		text-align: center;
	}
	textarea {
		width: 100%;
		margin-top: 15px;
	}
	textarea, input {
		width: 100%;
		height: 40px;
		padding: 10px;
		border: none;
		border-bottom: 2px solid #D5D6D5;
		outline: none;
		font-family: $sans-serif;
		font-style: italic;
		&:focus {
			border-bottom: 2px solid #F07D28;
		}
	}
	input.wpcf7-submit {
		width: 180px;
		margin-top: 50px;
		padding: 6px 0;
		background: transparent;
		border: 3px solid #F07B3C;
		border-radius: 50px;
		font-size: 12px;
		text-transform: uppercase;
		font-style: bold;
		color: #F07B3C;
		&:hover {
			background-color: #F07B3C;
			color: white;
		}
	}
	.wpcf7-form::after {
		content: " ";
		visibility: hidden;
		display: block;
		height: 0;
		clear: both;
	}
}

.front-page-contact {
	padding-top: 100px;
	background-color: $bkg-light;
	input, textarea {
		background-color: $bkg-light;
	}
}