header .menu {
	float: right;
	margin: 0;
	padding: 0;
	padding-top: 8px;
	ul {
		padding-top: 10px;
	}
	li {
		display: inline-block;
		margin-right: 10px;
		a {
			position: relative;
			font-size: 18px;
			padding: 10px;

			&:hover::after {
				@include keyline(calc(50% - 20px), 40px, #F07D28);
			}
		}
	}
}

#menu-social-navigation {
	float: left;
	li {
		display: inline-block;
		margin: 0;
		a {
			padding: 8px 10px;
			color: #7D7D7A;
			font-size: 20px;
			&:hover {
				color: white;
			}
		}
	}
}

