header {
	position: relative;
	.header-bar {
		padding: 45px 50px;
		background: rgba(255,255,255,0.4);
	}
}

.hero-image {
	height: 700px;
	background-size: cover;
	background-repeat: no-repeat;
	background-position: center;
	background-attachment: fixed;
}

.blog-hero-image {
	height: 400px;
}

.contact-about-hero-image {
	height: 500px;
	margin-bottom: 80px;
}

h1 {
	float: left;
}

.feature {
	padding-top: 150px;
	text-align: center;
	.feature-info {
		display: inline-block;
		width: 610px;
		padding: 45px 40px;
		text-align: left;
		vertical-align: top;
		color: white;
		text-shadow: 1px 1px 1px black;
		h2 {
			padding-bottom: 20px;
			font-size: 60px;
			font-family: $heading-font;
			font-weight: bold;
			color: white;
			text-transform: none;
			line-height: 1;
		}
		p {
			padding-bottom: 30px;
			letter-spacing: 1px;
		}
		p:first-of-type {
			font-family: $sans-serif;
			text-transform: uppercase;
		}
	}
}

.feature-book {
	position: relative;
	display: inline-block;
	max-width: 345px;
	border-radius: 2px;
	overflow: hidden;
	box-shadow: 0 15px 10px -6px #a8a7a7;
	img {
		display: block;
	}
	.feature-image {
		position: absolute;
		top: 0;
		right: 0;
		bottom: 0;
		left: 3%;
		box-shadow: -4px 0 10px rgba(0, 0, 0, 0.3), inset 3px 0 10px rgba(255, 255, 255, 0.3);
	}
}
